* {
  margin: 0;
  padding: 0;
}

.main {
  width: 100%;
  height: 100vh;
}

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: aliceblue;
  justify-content: space-around;
  padding: 5px;
}
.navOne {
  border: 1px white solid;
  padding-top: 1em;
  padding-right: 1.44em;
  padding-right: calc(1.44em - 0.2em);
  padding-bottom: 1em;
  padding-left: 1.44em;
  text-align: center;
  font-family: futura-pt;
  font-weight: 500;
  font-style: normal;
  font-size: 14px;

  letter-spacing: 0.2em;
  font-family: "Dosis", sans-serif;
}
.navTwo {
  border: 1px white solid;
  padding-top: 1em;
  padding-right: 1.44em;
  padding-right: calc(1.44em - 0.2em);
  padding-bottom: 1em;
  padding-left: 1.44em;
  text-align: center;
  font-family: futura-pt;
  font-weight: 500;
  font-style: normal;
  font-size: 14px;

  letter-spacing: 0.2em;
  font-family: "Dosis", sans-serif;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background-color: rgba(252, 226, 160, 0.2); */
}
